import React,{memo} from 'react'
import classNames from 'classnames'

function Button({ rounded, text, icon, iconPosition = 'left', color, position, onClick, style }) {
  const wrapperClasses = classNames(
    'button',
    {
      ['is-'+color]           : color,
      ['is-pulled-'+position] : position,
      'is-rounded'            : rounded,
    }
  )
  const iconClass = classNames({
    ['fas fa-'+icon] : icon,
  })

  const wrapperStyle = {
    ...style,
    width: (rounded && !text) ? 36 : 'auto', 
  }

  return (
    <div onClick={onClick} className={wrapperClasses} style={wrapperStyle}>
      {text ?
        (icon ? (
          iconPosition === 'left' ? <>
            <span className="icon">
              <i className={iconClass}></i>&nbsp;
            </span>
            <span>{text}</span>
          </> : <>
              <span>{text}</span>
              <span className="icon">
                &nbsp;<i className={iconClass}></i>
              </span>
          </>
        ) : <span>{text}</span>)
      : <i className={iconClass}></i>
    }
    </div>
  )
}

export default memo(Button)
