import React, { memo } from 'react'

import style from './style.module.scss'

function Title({ text, align }) {

  const titleStyle = {
    textAlign : align,
  }

  return (
    <div className={style.titleContainer}>
      <h2 className={style.titleText} style={titleStyle}>
        {text}
      </h2>
    </div>
  )
}

export default memo(Title)
